html {

scroll-behavior: smooth;

}

/* Em seu arquivo App.css */
body {
  margin: 0;
  padding: 0;
  font-family: "Pexico-Regular";
}

/* Deixe as regras para 'body' e '.App' como estão */

.background-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

header {
  position: sticky;
  background-color: black;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
  box-sizing: border-box; /* Garante que o padding não aumente a largura total */
}

.logo {
  font-size: 2em;
  color: #00FF7F; /* Cor verde do tema */
  font-weight: 600;
  text-shadow: 0 0 5px #00FF7F;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  margin-left: 20px;
}

nav a {
  text-decoration: none;
  color: #fff;
  font-weight: 500;
  font-size: 1.1em;
  transition: color 0.3s, text-shadow 0.3s;
}

nav a:hover {
  color: #00FF7F;
  text-shadow: 0 0 5px #00FF7F;
}

