/* Deixe as regras para 'body', '.App', e '.background-container' como estão */

.main-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 80%;
  height: 80%;
  z-index: 2;
  background-color: black;
  margin-top:100px;
  margin-left: 10%;
  border-radius: 40px;

  padding: 15px;


  /* Layout flex para alinhar os containers */
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4rem;
}

/* O container do texto precisa de um tamanho definido */
.text-ascii-container {
  position: relative; /* Cria um contexto para o texto absoluto dentro dele */
  width: 500px; /* Dê a ele uma largura fixa */
  height: 250px; /* E uma altura fixa */
  
}

.left-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; /* Centraliza o texto dentro do DecryptedText também */
}

.decrypted-text-wrapper {
  text-align: justify;
  font-size: 30px;
  margin-top: 4rem;
  margin-left: 4rem;
  max-width: 500px; /* Ajuda na quebra de linha */
  color: white !important;
}

/* O container do card não precisa de regras extras, pois o componente
   ProfileCard já tem seu próprio tamanho definido internamente. */
.Card-container {
  /* Vazio */
}

.sobreMim-container{
  display: flex;
  flex-direction: column;
  margin: 10%;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 80%;
  height: 720px;
  scroll-margin-top: 100px; /* Adicionado para compensar o header */

    background-color: black !important;
  
}

.buttons-sobreMim {
  display: flex;
  justify-items: center;
  gap: 1rem;
  margin-top: 2rem;
}

.btn-social-item {
  display: inline-flex;
  align-items: center;
  padding: 0.7rem 1.2rem;
  border: 2px solid #00FF7F;
  color: #00FF7F;
  text-decoration: none;
  font-size: 1.2em;
  font-family: 'Pexico-Regular', sans-serif;
  transition: background-color 0.3s, color 0.3s;
  border-radius: 5px;
   text-shadow: 0 0 5px #00FF7F;
}

.btn-social-item:hover {
  background-color: #00FF7F;
  color: black;
}

.btn-social-item i {
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.placeholder-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 500px; /* Altura de exemplo */
  width: 100%;
  color: white;
  scroll-margin-top: 100px; /* Compensa o header */
}

/* --- Media Queries para Responsividade --- */

@media (max-width: 1200px) {
  .main-container {
    gap: 2rem; /* Diminui o espaço entre os itens em telas médias */
  }
}

@media (max-width: 992px) {
  /* Ajusta o cabeçalho para telas menores */
  header {
    padding: 20px 5%;
  }

  /* Empilha o conteúdo principal verticalmente */
  .main-container {
    flex-direction: column;
    height: auto; /* Permite que a altura cresça com o conteúdo */
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    top: 80px;
    margin-top: 0;
    padding: 2rem;
  }

  .text-ascii-container {
    width: 100%;
    max-width: 450px;
    height: 200px;
  }

  .decrypted-text-wrapper {
    margin-left: 0;
    font-size: 24px;
    text-align: center;
    margin-top: 2rem;
  }

  .sobreMim-container {
    height: auto; /* Deixa o conteúdo definir a altura */
    min-height: 50vh;
    padding-top: 5rem;
    padding-bottom: 5rem;
    background-color: black !important;
  }
}

@media (max-width: 768px) {
  /* Empilha os itens do cabeçalho e o torna parte do fluxo da página */
  header {
    flex-direction: column;
    gap: 1rem;
    position: static; /* Importante para o fluxo em telas móveis */
  }

  .main-container {
    position: static; /* Também se torna parte do fluxo normal */
    border-radius: 0;
    width: 100%;
    margin: 0;
    box-sizing: border-box;
  }
}

@media (max-width: 480px) {
  .decrypted-text-wrapper {
    font-size: 18px;
  }

  .buttons-sobreMim {
    flex-direction: column;
    align-items: center;
  }

  .btn-social-item {
    width: 80%;
    justify-content: center;
  }
}